import React, {useEffect, useState} from "react";
import BannerFinalCart from "./Final Cart/BannerFinalCart";
import MainFinalCart from "./Final Cart/MainFinalCart";
import {useNavigate} from 'react-router-dom';
import { useCookies } from "react-cookie";
import Loading from "./Loading";

function FinalCart(props){
  let [auth, setAuth] = useState(false);
  let [cookie, setCookie] = useCookies('cart');
  let nav = useNavigate();
  useEffect(()=>{
    if(!cookie.cart){
      nav('/');
    }else{
      setAuth(true);
    }
    document.title = "HM Shop | CheckOut";
  }, [cookie.cart]);
    return (
      <>
      {auth ? 
      <div className="container mx-auto px-2 xl:px-0 flex flex-col">
      <BannerFinalCart />
      <MainFinalCart />
    </div>
    :
    <Loading/>
    }
      </>
    )
}

export default FinalCart;